var title1 = "Новости ещё не загрузились, для обновления нажмите кнопку \"Назад\", подождите и снова зайдите в раздел \"Новости\", если новости не появятся через 10 секунд, то очистите кеш";
var title2 = "Если у вас слабый интернет то ждать нужно чуть дольше чем 10 секунд, но этого времени вполне достаточно чтобы загрузить хотябы пару новостей";
