var title4 = "*Новость*";
var title5 = "*Новость*";


var doc = undefined;
var request = new XMLHttpRequest();
// request.header = 'Access-Control-Allow-Origin: *';
request.open("GET", news_url, true);
request.send();
request.onreadystatechange = function() {
// eslint-disable-next-line
  if (request.readyState == 4 && request.status == 200) {
        html_page = request.responseText
        var parser = new DOMParser();
        doc = parser.parseFromString(html_page, "text/html");
    }
};


connect.send('VKWebAppInit', {});
setTimeout(function() {VKConnect.send('VKWebAppGetUserInfo', {})}, 2000)

const osname = platform();

const itemStyle = {
    flexShrink: 0,
    width: 80,
    height: 94,
    display: 'flex',
    flexDirection:
    'column',
    alignItems: 'center',
    fontSize: 12,
};

function Favor() {
    connect.send("VKWebAppAddToFavorites", {});
}


//                                    -+=|  TO DELETE  |=+--
// eslint-disable-next-line
function Share() {
    connect.send("VKWebAppShare", {"link": "https://vk.com/app123456"});
}

function Donate(pay_sum) {
    connect.send("VKWebAppOpenPayForm", { 
        "app_id": 7021354, 
        "action": "pay-to-group", 
        "params": {
            "amount": pay_sum, 
            "description": "тестовый платеж", 
            "group_id": 181318900 
        } 
    });
}
